import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map'
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';
import { Dominio } from '../../model/dominio';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  })
};

@Injectable({
  providedIn: 'root'
})
export class DominioService {

  private url: string = environment.url_estrutura;

  private readonly API = this.url+'/api/govc/v1/dominios/';

  constructor(private http: HttpClient) {
    console.log('DominioService');
  }

  findByTipoOrderByNome(tipo: string): Observable<Dominio[]> {
    return this.http.get<Dominio[]>(
      this.API.concat(tipo))
      .pipe(map((result:any)=>{
        if(result == null || result == undefined) {
          return null;
        }
        return result._embedded.content;
      }));
  }

}
